<template>
  <div className="filter-group">
    <div className="search-input">
      <a-input type="请输入关键字" v-model="searchValue"/>
    </div>
    <better-scroll>
    </better-scroll>
  </div>
</template>

<script>
import {BetterScroll} from '@handday/components'

export default {
  name: 'FilterGroup',
  components: {
    BetterScroll
  },
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      searchValue: ''
    }
  },
  computed: {
    modelValue: {
      set(value) {
        this.$emit('input', value)
      },
      get() {
        return this.value
      }
    }
  },
  methods: {
    /**
     * 值发生变化
     * */
    changeHandle() {
      this.$nextTick(() => {
        this.$emit('change', this.modelValue)
      })
    },
    /**
     * 设置值
     * */
    setData(value) {
      this.modelValue = value
    },
    /**
     * 选择值
     * */
    getData() {
      return this.modelValue
    },
    /**
     * 重置
     * */
    resetData() {
      this.modelValue = [undefined, undefined]
      this.$nextTick(() => {
        this.$emit('change', this.value)
      })
    }
  }
}
</script>

